@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.search__suggest__option {
  padding: 4px 8px;
  background-color: white;
  cursor: pointer;
}
.search__suggest__option:nth-child(even) {
  background-color: #F0F8FF	;

}
.search-bg {
  background-image: url(/Image-search/static/media/main-bg.5360d362.jpg);
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.search__title {
  color: white;
  font-size: 64px;
}
.search__input {
  outline: none;
  border-radius: 4px;
  height: 28px;
  width: 320px;
  border: none;
  padding: 4px 8px;

  box-shadow: 0 0 1em blue;
}
.search__icon {
  position: absolute;
  top: 6px;
  right: 6px;
  cursor: pointer;
}
.search {
  position: relative;
}
.search__suggest {
  width: 100%;
}

.search__none {
  color: white;
}
.results {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  overflow-x: hidden;
}
.results__header {
  position: absolute;
  align-self: flex-start;
  font-size: 40px;
  text-transform: capitalize;
  top: 106px;
  font-weight: 800;
}
.results__tags {
  display: flex;
  width: 100%;
  margin-top: 120px;
  flex-wrap: wrap;
}
.result{
  margin: 0;
  display: grid;
  grid-template-rows: 1fr auto;
  margin-bottom: 10px;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
  cursor: pointer;
}
.results__tag {
    padding: 4px 6px;
    margin: 24px 12px;
    box-shadow: 0 0 1em blue;
    border-radius: 4px;
    height: 20px;
    white-space: nowrap;
}
.results .search__suggest {
  position: absolute;
  top: 46px;
  overflow: hidden;
}
.result__tags{
    display: flex;
  
}
.result__img{
  grid-row: 1 / -1;
  grid-column: 1;
  width: 100%;
}
.results__list{
    
  width: 100%;
  -webkit-column-count: 4;
          column-count: 4;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;  
 }
.result__overlay{
   position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  cursor: pointer;
}
.result__overlay__inner{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 40px;
  left: 60px;
  font-size: 50px;
  width: calc(100% - 160px);
  background-color: white;
  padding: 16px;
  border-radius: 4px;
  box-shadow: 0 0 0 9999px rgba(0,0,0,0.8);
}
.overlay__author{
  display: flex;
  align-self: flex-start;
}
.overlay__author__column__img{
  height: 64px;
}
.overlay__img{
  width: 80%;
}
.overlay__location{
  display: flex;
    justify-content: center;
    align-items: center;
}
.overlay__author__column__name{
  line-height: 32px;
  font-size: 24px;
  color:black;
}
.overlay__location{
  line-height: 32px;
  font-size: 24px;
  color:black;
}
.overlay__img{
 width: 60%;
}

.overlay__close{
  position: absolute;
    right: 16px;
}
@media (max-width: 1400px) {
  .results__list{
    
    width: 100%;
    -webkit-column-count: 3;
            column-count: 3;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;  
   }
}
@media (max-width: 1100px) {
  .results__list{
    
    width: 100%;
    -webkit-column-count: 2;
            column-count: 2;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;  
   }
}
@media (max-width: 1000px) {
  .search__input {


}

  .results__list{
    
display: flex;
flex-direction: column;
-webkit-column-count: 1;
        column-count: 1;
   }
   .search__title {
    font-size: 44px;
}
}
@media (max-width: 350px) {
.search__input {

  width: 300px;

}
.search__icon {

  right: 18px;
}
}
